'use client'
import { useState, useEffect, useRef, useCallback, useMemo } from "react";
import dynamic from "next/dynamic";
import "@/style/globals.css";
import Loading from "@/components/Loading";
import clsx from "clsx";
import Header from "@/components/Header"; // Import Header


// Debounce function to limit the rate of execution
const debounce = (func: Function, wait: number) => {
  let timeout: NodeJS.Timeout;
  return function (this: any, ...args: any[]) {
    const context = this;
    clearTimeout(timeout);
    timeout = setTimeout(() => func.apply(context, args), wait);
  };
};

export default function RootLayout({ children }: { children: React.ReactNode }) {
  const [isSidebarOpen, setIsSidebarOpen] = useState(false);
  const [isSmallScreen, setIsSmallScreen] = useState<boolean | null>(null);
  const [loading, setLoading] = useState(false);
  const [hydrated, setHydrated] = useState(false);
  const touchStartX = useRef(0);

  // Handle window resize with debounce
  const handleResize = useCallback(() => {
    setIsSmallScreen(window.innerWidth < 640);
  }, []);

  useEffect(() => {
    setHydrated(true);
    handleResize();
    const debouncedHandleResize = debounce(handleResize, 200);
    window.addEventListener("resize", debouncedHandleResize);
    return () => {
      window.removeEventListener("resize", debouncedHandleResize);
    };
  }, [handleResize]);

  // Toggle sidebar
  const toggleSidebar = useCallback(() => {
    setIsSidebarOpen((prev) => !prev);
  }, []);

  // Handle touch start event
  const handleTouchStart = useCallback((e: React.TouchEvent) => {
    touchStartX.current = e.touches[0].clientX;
  }, []);

  // Handle touch move event
  const handleTouchMove = useCallback((e: TouchEvent) => {
    const touchEndX = e.touches[0].clientX;
    if (touchStartX.current - touchEndX > 100) {
      setIsSidebarOpen(false);
    }
  }, []);

  // Add/remove touch move event listener based on sidebar state
  useEffect(() => {
    if (isSidebarOpen) {
      document.addEventListener("touchmove", handleTouchMove);
    } else {
      document.removeEventListener("touchmove", handleTouchMove);
    }
    return () => {
      document.removeEventListener("touchmove", handleTouchMove);
    };
  }, [isSidebarOpen, handleTouchMove]);

  // Memoized Header and Sidebar to prevent unnecessary re-renders
  const memoizedHeader = useMemo(() => (
    <Header toggleSidebar={toggleSidebar} isSmallScreen={isSmallScreen ?? false} />
  ), [toggleSidebar, isSmallScreen]);

  const memoizedSidebar = useMemo(() => (
    <Sidebar />
  ), []);

  if (!hydrated) return null;

  return (
    <div className="h-screen overflow-hidden">
      {loading ? (
        <Loading />
      ) : (
        <div className="flex h-full relative">
          <div
            aria-hidden={!isSidebarOpen}
            className={clsx(
              "transform transition-all duration-300 ease-in-out z-50 fixed inset-0 sm:relative w-64 text-white",
              { hidden: isSmallScreen && !isSidebarOpen },
              { block: isSmallScreen ? isSidebarOpen : true }
            )}
            onTouchStart={handleTouchStart}
          >
            {isSmallScreen !== null && memoizedSidebar}
          </div>
          <div className="flex-1 overflow-auto ml-1 relative">
            {/* Memoized Header */}
            {memoizedHeader}
            {/* Main content */}
            <div className="p-3 sm:p-3 lg:p-6 mt-[70px]"> {/* margin-top bằng chiều cao Header */}
              {children}
            </div>
          </div>
        </div>
      )}
    </div>

  );
}